<template>
    <div>
        <h1>问题列表</h1>
        <el-table :data="items">
            <el-table-column prop="_id" label="ID" width="240"></el-table-column>
            <el-table-column prop="name" label="题目"></el-table-column>
            <!-- <el-table-column prop="year" label="年" width="60px"></el-table-column> -->
            <el-table-column prop="month" label="月" ></el-table-column>
            <el-table-column prop="icon" label="图标">
                <template slot-scope="scope">
                    <img :src="scope.row.data" style="height:50px; width:50px">
                </template>
            </el-table-column>
            <el-table-column fixed="right" label="操作" width="180">
                <template slot-scope="scope">
                    <el-button type="primary" size="small" 
                    @click="$router.push(`/items/edit/${scope.row._id}`)">编辑</el-button>
                    <el-button type="primary" size="small" 
                    @click="remove(scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
export default {
    data(){
        return{
            items:[]
        }
    },
    methods:{
        async fetch(){
            const res = await this.$http.get('rest/items')
            this.items = res.data
        },

        async remove(row){
            this.$confirm(`是否确定删除"${row.name}"?`, '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            })
            .then( async() => {
                const res =await this.$http.delete(`rest/items/${row._id}`) // eslint-disable-line no-unused-vars
                this.$message({
                type: 'success',
                message: '删除成功!',
                
            });
            this.fetch()
            })
        }
    },
    created(){
        this.fetch()
    }
}
</script>
